<template>
  <div class="listmenu">
    <!-- <el-button type="primary" @click="openFullScreen2"> 服务方式 </el-button> -->
    <el-table
      :data="MemberGetter"
      style="width: 100%; margin-bottom: 20px"
      row-key="id"
      border
      :tree-props="{ children: 'children', hasChildren: 'hasChildren' }"
    >
      <el-table-column prop="id" label="用户编号" sortable width="150">
      </el-table-column>

      <el-table-column
        prop="nickname"
        label="昵称"
        sortable
        width="300"
      ></el-table-column>

      <el-table-column
        prop="phone"
        label="手机号"
        sortable
        width="300"
      ></el-table-column>

      <el-table-column prop="address" label="操作">
        <template slot-scope="scope">
          <div>
            <!-- 这里自定义 表格 在template中 添加 slot-scope = scope -->
            <!-- 判断状态 如果说 后台返回的 status 是 1 就是启用 否则就禁用  -->
            <el-button type="primary" v-if="scope.row.status == 1" plain
              >启用</el-button
            >

            <el-button type="info" v-else plain>禁用</el-button>

            <el-button type="success" @click="edit(scope.row.uid)" plain
              >编辑</el-button
            >

          </div>
        </template>
      </el-table-column>
    </el-table>

   
  </div>
</template>
<script>
// 引入仓库

import { mapGetters, mapActions } from "vuex";
import { successAlert, warningAlert } from "../../../util/alert";

export default {
  computed: {
    ...mapGetters({
      // 菜单请求
      MemberGetter: "member/MemberGetter",
    
    }),
  },

  methods: {
    ...mapActions({
      actionMemberList:"member/actionMemberList"
    }),

    // 编辑函数
    edit(id) {
      // 给父组建进行传参数
      this.$emit("listedit", id);
    },
  },

  data() {
    return {
      flag: true,
    };
  },

  // 挂载完成之后 进行 调用 发送请求函数
  mounted() {
   this.actionMemberList()
  },
};
</script>

<style lang="">
.cell {
  text-align: center;
}

.el-table,
.submit {
  width: 100%;
  margin-top: 100px;
}
.submit {
  font-size: 16px;
}
</style>


