<template>
  <div>
    <el-dialog
      :title="isShow.edit ? '添加菜单' : '修改菜单'"
      :visible.sync="isShow.flag"
      @close="cancel"
    >
      <el-form ref="form" label-width="100px">
        <el-form-item label="手机号:">
          <el-input v-model="form.phone"></el-input>
        </el-form-item>

        <el-form-item label="用户名:">
          <el-input v-model="form.nickname"></el-input>
        </el-form-item>

        <el-form-item label="密码:">
          <el-input v-model="form.password"></el-input>
          <span class="p">留空不变</span>
        </el-form-item>

        <el-form-item label="状态" prop="delivery">
          <!-- active-value="1" :inactive-value = "2" -->
          <!-- 设置 什么值打开 什么值关闭  -->
          <el-switch
            v-model="form.status"
            :active-value="1"
            :inactive-value="2"
          ></el-switch>
        </el-form-item>
      </el-form>

      <div slot="footer" class="dialog-footer">
        <el-button @click="cancel()">取 消</el-button>

        <!-- 修改按钮 通过 v-if控制  -->
        <el-button type="primary" @click="edit()">修 改</el-button>
      </div>
    </el-dialog>
  </div>
</template>


<script>
import { mapGetters, mapActions } from "vuex";
import { successAlert, warningAlert } from "../../../util/alert";
import { reqMemberInfo,reqMemberEdit} from "../../../util/request";

export default {
  props: ["isShow"],

  computed: {
    ...mapGetters({
      MemberGetter:".member/MemberGetter"
    }),
  },

  methods: {
    ...mapActions({
      actionMemberList:"member/actionMemberList"
    }),

    // 取消函数
    cancel() {
      this.form = {
        nickname: "",
        phone: "",
        password: "",
        status: 1,
      };
      this.isShow.flag = false;
    },

    // 进行 获取某一条数据并添加进去
    getOne(id) {
      reqMemberInfo(id).then((res) => {
        this.form = res.data.list;
        this.pas = this.form.password;
        this.form.password = "";
        this.form.uid = id;
      });
    },

    // 进行修改
    edit() {
      if ((this.form.password = "")) {
        this.form.password = this.pas;
      }

      reqMemberEdit(this.form).then((res) => {
        if (res.data.code == 200) {
          successAlert(res.data.msg);
          this.actionMemberList()
          this.cancel();
        } else {
          warningAlert(res.data.msg);
        }
      });
    },
  },

  data() {
    return {
      form: {
        nickname: "",
        phone: "",
        password: "",
        status: 1,
      },
      // 这个用来记录一下 当前的密码 如果刘空就进行赋值 保证 原密码不便
      pas: "",
    };
  },
};
</script>

<style>
</style>




